var string = "<table class='organization-listing' title=\"<%- model.get('name') %>\">\n  <tr>\n    <td><a href='#' class='organization-name'><%- model.get('name') %></a></td>\n  </tr>\n  <% if(model.get('website')) { %>\n    <tr>\n      <td><a href=\"<%- model.get('website') %>\"><%- model.get('website') %></a></td>\n    </tr>\n  <% } %>\n  <% var attributes = ['email_1', 'email_2', 'phone_1', 'phone_2', 'address_1', 'address_2'] %>\n  <% _.each(attributes, function(attribute) { %>\n    <% if(model.get(attribute)) { %>\n      <tr>\n        <td><%- model.get(attribute) %></td>\n      </tr>\n    <% } %>\n  <% }) %>\n\n  <% if(model.cityStateZip()) { %>\n    <tr>\n      <td><%- model.cityStateZip() %></td>\n    </tr>\n  <% } %>\n</table>";

module.exports = string;
